<template>
  <header class="home-header">
    <div class="left-container">
      <div class="page-name">
        <el-icon @click="backPage(-1)"><arrow-left /></el-icon>
        <el-breadcrumb>
          <el-breadcrumb-item
            v-for="(item, index) in matched"
            :key="index"
            :to="{ path: item.path }"
            >{{ item.meta.pageName }}</el-breadcrumb-item
          >
        </el-breadcrumb>
      </div>
    </div>
    <div class="right-container">
      <!--   邀请下属   -->
      <el-popover
        placement="bottom"
        :width="630"
        trigger="hover"
        style="padding: 0"
      >
        <template #reference>
          <div class="invitation">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-qr-code"></use>
            </svg>
            <span class="word">邀请下级</span>
          </div>
        </template>
        <ul class="invitation-list">
          <li class="item" v-for="(item, index) in invitationList" :key="index">
            <h6>{{ item["title"] }}</h6>
            <div class="QR-code">
              <div :id="`qrCode${index}`"></div>
            </div>
            <span class="word">【扫描二维码注册】</span>
            <span class="prompt">复制链接注册成为{{ item["text"] }}</span>
            <span
              :id="item.id"
              class="copy-button"
              :data-clipboard-text="`${item['copyUrl']}`"
              >点击复制</span
            >
          </li>
        </ul>
      </el-popover>
      <!--   用户信息   -->
      <div class="user-info">
        <span class="name">{{ userInfo?.realName }}</span>
        <el-dropdown @command="handleCommand">
          <img
            v-if="userInfo?.portraitImgUrl"
            class="head-portrait"
            :src="userInfo?.portraitImgUrl"
            alt=""
            @error="handlePortrait"
          />
          <img
            v-else
            class="head-portrait"
            src="@/assets/images/man-head-portrait.png"
            alt=""
          />
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item command="signOut">退出</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </div>
  </header>
</template>

<script lang="ts" setup>
import { computed, onMounted, ref } from "vue";
import { ArrowLeft } from "@element-plus/icons-vue";

import { LocalStorage, SessionStorage } from "jz-tool-lib";
import { environmentOrigin } from "@assets/common";
import { useClipboard, useQRCode, useVueRouter } from "@assets/useHook";

const { route, goPage, backPage } = useVueRouter();
const userInfo = LocalStorage.getItem("bd_userInfo");
const matched = computed(() => route.matched.slice(1, route.matched.length));
const invitationList = ref([
  {
    type: "saas",
    title: "邀请下级客户",
    text: "BDSaaS用户",
    copyUrl: `${environmentOrigin().bdSaas}/reg?inviterId=${
      userInfo?.profileId
    }`,
    qrcode: "",
    id: "copy-saas",
  },
  {
    type: "daiLi",
    title: "邀请下级服务商",
    text: "服务商",
    copyUrl: `${environmentOrigin().bdDaiLi}/#/register?inviterId=${
      userInfo?.profileId
    }`,
    qrcode: "",
    id: "copy-daiLi",
  },
]);
const handleCommand = (command) => {
  // 退出登录
  if (command === "signOut") {
    const loginForm = LocalStorage.getItem("bd_loginForm");
    LocalStorage.clear();
    SessionStorage.clear();
    loginForm && LocalStorage.setItem("bd_loginForm", loginForm);
    goPage("/");
  }
};
// 点击复制
const initClipboard = () => {
  useClipboard({
    id: "copy-saas",
  });
  useClipboard({
    id: "copy-daiLi",
  });
};
// 生成二维码
const initQRCode = (dom, url) => {
  return useQRCode({
    dom,
    text: url,
    width: 120,
    height: 120,
  });
};
// 处理头像图片
function handlePortrait(e) {
  e.target.src =
    userInfo.gender === "1"
      ? require("@/assets/images/man-head-portrait.png")
      : require("@/assets/images/woman-head-portrait.png");
}
onMounted(() => {
  initClipboard();
  invitationList.value.map((item, index) => {
    item.qrcode = initQRCode(`qrCode${index}`, item.copyUrl);
    return item;
  });
});
</script>

<style scoped lang="scss">
.home-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 68px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  padding: 0 32px;
  background-color: #ffffff;
}

.left-container {
  .page-name {
    display: flex;
    align-items: center;

    .el-icon {
      font-size: 18px;
      margin-right: 6px;
      cursor: pointer;
    }
  }
  :deep(.el-breadcrumb) {
    font-size: 15px;
    .el-breadcrumb__inner {
      font-weight: 900;
      color: #303133;
    }
    .el-breadcrumb__item:last-child {
      .el-breadcrumb__inner {
        font-weight: initial;
        color: #a8abb2;
      }
    }
  }
}

.right-container {
  display: flex;
  align-items: center;

  .invitation {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 128px;
    height: 36px;
    color: #ffffff;
    background: #0056ff;
    border-radius: 18px;
    margin-right: 65px;
    cursor: pointer;

    .icon-qr-code {
      width: 15px;
      height: 15px;
    }

    .word {
      font-size: 14px;
      font-weight: 500;
      line-height: 20px;
      margin-left: 10px;
    }
  }

  .user-info {
    display: flex;
    align-items: center;

    .name {
      font-size: 14px;
      font-weight: 500;
      color: #646b7a;
      line-height: 20px;
      margin-right: 20px;
    }

    .head-portrait {
      display: flex;
      width: 38px;
      height: 38px;
      cursor: pointer;
      border-radius: 100%;
      border: 1px solid #e6e6e6;
    }
    .head-portrait:focus {
      outline: 0 !important;
    }
  }
}

:deep(.el-popover.el-popper) {
  padding: 0;
}

.invitation-list {
  display: flex;

  .item {
    flex: 0 0 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: 1px solid #e4e7ed;

    &:last-child {
      border-right: none;
    }

    h6 {
      font-size: 20px;
      font-weight: 500;
      color: #242934;
      line-height: 1;
      text-align: center;
      margin-top: 30px;
    }

    .QR-code {
      width: 120px;
      height: 120px;
      margin: 40px 0 20px;
    }

    .word {
      font-size: 14px;
      color: #646b7a;
      line-height: 14px;
    }

    .prompt {
      font-size: 14px;
      color: #646b7a;
      line-height: 14px;
      margin: 48px 0 16px;
    }

    .copy-button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100px;
      height: 36px;
      color: #0056ff;
      font-size: 14px;
      cursor: pointer;
      background: rgba(0, 86, 255, 0.03);
      border-radius: 4px;
      margin-bottom: 38px;
    }
  }
}
</style>
