<template>
  <div class="main">
    <HomeAside :routerList="paramsData.routerList"></HomeAside>
    <div class="entry-container">
      <HomeHeader></HomeHeader>
      <div class="entry-content">
        <router-view />
      </div>
    </div>
    <!--  服务到期提示  -->
    <ServiceExpirationDialog
      v-model:visible="paramsData.serviceExpirationVisible"
    ></ServiceExpirationDialog>
    <!--  系统禁用提示  -->
    <SystemDisabledDialog
      v-model:visible="paramsData.systemDisabledVisible"
    ></SystemDisabledDialog>
    <!--  认证通过提示  -->
    <CertificationStatusDialog
      v-model:visible="paramsData.certificationStatusVisible"
      :certificationStatus="paramsData.certificationStatus"
      :checkAdvice="paramsData.checkAdvice"
    ></CertificationStatusDialog>
  </div>
</template>
<script lang="ts" setup>
import { reactive, defineAsyncComponent } from "vue";

import menuRouter from "@router/menu-router";
import { useVueRouter } from "@/assets/useHook";
import { LocalStorage } from "jz-tool-lib";

import { certificationStatusApi, expireInformationApi } from "@api/common-api";

import HomeAside from "@/components/main/main-aside.vue";
import HomeHeader from "@/components/main/main-header.vue";
const SystemDisabledDialog = defineAsyncComponent(
  () => import("@components/main/system-disabled-dialog.vue")
);
const ServiceExpirationDialog = defineAsyncComponent(
  () => import("@components/main/service-expiration-dialog.vue")
);
const CertificationStatusDialog = defineAsyncComponent(
  () => import("@components/common/certification-status-dialog.vue")
);

const { route, replacePage, onBeforeRouteUpdate } = useVueRouter();
const paramsData = reactive({
  systemDisabledVisible: false,
  serviceExpirationVisible: false,
  certificationStatusVisible: false,
  certificationStatus: LocalStorage.getItem("bd_checkStatus"),
  routerList: menuRouter,
  checkAdvice: "",
});

// 获取认证状态
const getCertificationStatus = async () => {
  try {
    const { data } = await certificationStatusApi.getData();
    paramsData.checkAdvice = data.checkAdvice;
    paramsData.certificationStatus = data.checkStatus;
    LocalStorage.batchSetItem({
      bd_agentId: data?.id, // 代理商id
      bd_checkAdvice: data?.checkAdvice, //  认证驳回原因
      bd_checkStatus: data?.checkStatus, //  公司是否认证状态
      bd_virCompanyId: data?.virCompanyId, //代理商公司id
      bd_existCallInterface: data?.existCallInterface, //购买业务类型
    });
    routerChange(data?.msgReadState, data?.existCloudCall);
  } catch (e) {
    routerChange(1, true);
  }
};

const { bd_existCallInterface: existCallInterface } = LocalStorage.batchGetItem(
  ["bd_existCallInterface"]
);
const routerChange = (msgReadState: number, existCloudCall: boolean) => {
  // 根据状态显示不同侧边栏
  paramsData.routerList = menuRouter.filter((item: any) => {
    return (
      item.meta.role &&
      item.meta.role.indexOf(paramsData.certificationStatus) !== -1
    );
  });
  paramsData.routerList.map((item: Record<string, any>) => {
    if (item.name === "financialStatistics" && !existCloudCall) {
      item.children = item.children.filter((sonItem) => {
        return sonItem.name !== "advanceCharge";
      });
    }
    if (item.name === "financialCenter") {
      item.children = item.children.filter((sonItem) => {
        if (existCallInterface === 0) {
          return sonItem.name !== "financialCenter.agencyPrice";
        }
        return sonItem;
      });
    }
    return item;
  });
  if (paramsData.certificationStatus === 2) {
    if (route.path === "/certificationService") {
      replacePage("/customerManagement/personalCustomer/list");
    }
    if (msgReadState === 0) {
      paramsData.certificationStatusVisible = true;
    }
  }
};
// 查询代理产品代理权过期信息
const expireInformation = () => {
  expireInformationApi.getData();
};
expireInformation();
getCertificationStatus();
routerChange(1, true);
// 每次路由变化都要获取当前代理商的资格认证状态
onBeforeRouteUpdate(() => {
  getCertificationStatus();
});
</script>
<style lang="scss" scoped>
.main {
  display: flex;
  height: 100%;
  background: #f5f7fb;
  overflow: hidden;
}
.entry-container {
  position: relative;
  width: calc(100% - 220px);
  height: 100%;
  overflow: hidden;
  .entry-content {
    width: 100%;
    height: 100%;
    overflow-x: scroll;
  }
}
</style>
