<template>
  <aside class="main-aside">
    <div v-if="['ec-line'].includes(systemName)" class="logo-name">
      服务商系统
    </div>
    <div v-else class="logo"></div>
    <el-menu
      class="el-menu-vertical-demo"
      :default-active="initData.defaultMenu"
      router
      unique-opened
    >
      <template v-for="item in routerList" :key="item.path">
        <el-menu-item
          :index="item.path"
          v-if="
            (!item.children && !item.meta.hideMenu) || item.meta.filterSubMenu
          "
        >
          <svg class="menu-icon" aria-hidden="true">
            <use :xlink:href="`#${item.meta.iconName}`"></use>
          </svg>
          <template #title
            ><span class="title">{{ item.meta.pageName }}</span></template
          >
        </el-menu-item>
        <el-sub-menu
          :index="`${item.path}`"
          v-if="
            item.children && !item.meta.hideMenu && !item.meta.filterSubMenu
          "
        >
          <template #title>
            <svg class="menu-icon" aria-hidden="true">
              <use :xlink:href="`#${item.meta.iconName}`"></use>
            </svg>
            <span class="title">{{ item.meta.pageName }}</span>
          </template>
          <template v-for="sonItem in item.children" :key="sonItem.path">
            <el-menu-item
              v-if="!sonItem.meta.hideMenu"
              :index="`${sonItem.path}`"
            >
              <span class="title">{{ sonItem.meta.pageName }}</span>
            </el-menu-item>
          </template>
        </el-sub-menu>
      </template>
    </el-menu>
  </aside>
</template>

<script lang="ts" setup>
import { watch, shallowReactive } from "vue";

import { systemName } from "@assets/common";
import { useVueRouter } from "@/assets/useHook";

const { route } = useVueRouter();
defineProps<{
  routerList: Array<any>;
}>();
const initData = shallowReactive({
  defaultMenu: "",
});
watch(
  route,
  (value) => {
    if (value.meta.defaultActiveMenu) {
      // 类似列表的详情页才会走这
      (initData.defaultMenu as any) = value.meta.defaultActiveMenu;
    } else {
      if (value.matched.length > 3) {
        initData.defaultMenu = value.matched[value.matched.length - 2].path;
      } else {
        initData.defaultMenu = value.matched[value.matched.length - 1].path;
      }
    }
  },
  {
    immediate: true,
  }
);
</script>

<style scoped lang="scss">
.main-aside {
  flex: 0 0 auto;
  width: 220px;
  height: 100%;
  padding: 0 10px;
  background-color: rgba(40, 44, 65, 0.95);
}

.logo {
  width: 200px;
  height: 68px;
  background: url("../../assets/images/logo-header-menu.png") no-repeat center;
  background-size: 154px 23px;
  margin-bottom: 25px;
  box-shadow: 5px 0 30px 0 rgba(50, 94, 181, 0.05);
}

.logo-name {
  width: 200px;
  height: 80px;
  line-height: 80px;
  text-align: center;
  font-size: 15px;
  font-weight: 600;
  color: #ffffff;
}

.el-menu {
  margin: 0 auto;
  border-right: none;
  background-color: transparent;

  &:not(.el-menu--collapse) {
    width: 180px;
  }

  :deep(.el-menu-item),
  :deep(.el-sub-menu) {
    padding-right: 0;
    margin-bottom: 20px;

    .title {
      font-size: 14px;
      color: #ffffff;
      line-height: 14px;
    }

    .el-icon {
      font-size: 14px;
      color: #ffffff;
      line-height: 14px;
      margin-right: 20px;

      &.el-sub-menu__icon-arrow {
        margin-right: 0;
      }
    }
  }

  :deep(.el-menu-item) {
    height: 40px;
    line-height: 40px;
    border-radius: 4px;

    &:hover,
    &.is-active {
      background-color: #0056ff;
    }
  }

  :deep(.el-sub-menu) {
    .el-sub-menu__title {
      height: 40px;
      padding-right: 0;
      line-height: 40px;
      border-radius: 4px;
      margin-bottom: 5px;

      &:hover {
        border-radius: 4px;
        background-color: #0056ff;
      }
    }

    &.is-active {
      .el-sub-menu__title {
        background-color: #0056ff;
      }
    }
  }

  &.el-menu--collapse {
    :deep(.el-sub-menu) {
      .title,
      .el-sub-menu__icon-arrow {
        display: none;
      }
    }
  }

  .el-sub-menu {
    :deep(.el-menu) {
      background-color: #23273a;
    }

    :deep(.el-menu--inline) {
      z-index: 2;
      overflow: hidden;
      padding: 0 10px;
      border-radius: 4px;
      background-color: #23273a;

      .el-menu-item {
        justify-content: center;
        min-width: 100%;
        margin-bottom: 10px;
        padding-left: 0 !important;

        &:first-child {
          margin-top: 15px;
        }

        &:last-child {
          margin-bottom: 15px;
        }

        &:hover,
        &.is-active {
          border-radius: 4px;
          background-color: rgba(255, 255, 255, 0.05);
        }
      }
    }
  }

  .menu-icon {
    width: 20px;
    height: 20px;
    color: #ffffff;
    margin-right: 15px;
  }
}
</style>
